.title {
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  margin: 0 0 18px 0 !important;
}

.input {
  width: 100%;
  margin-bottom: 12px;
  height: 45px;
  border-radius: 6px;
  border: none;
  outline: none;
  text-align: center;
  color: #230a40;
  font-size: 22px;
  font-weight: 700;
  line-height: 28px;
}

.input::placeholder {
  color: #230a40;
}

.coinHolder {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  gap: 6px;
}

.coinHolder li label {
  border-radius: 30px;
  border: 1px solid #fff;
  background-color: rgba(77, 14, 113, 0.35);
  padding: 7px 10px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: #fff;
  transition: all 0.5s ease 0s;
  cursor: pointer;
}

.coinHolder li .icon {
  flex-shrink: 0;
  width: 15px;
  height: 15px;
  object-fit: cover;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 45px;
  border: none;
  border-radius: 50px;
  background-color: #ffcc00;
  font-size: 22px;
  font-weight: 700;
  color: #230a40;
  transition: all 0.5s ease 0s;
  width: 100%;
}

.button:hover {
  background-color: #230a40;
  color: #ffcc00;
}
