.container {
  width: 100%;
  height: 100%;
  position: relative;
}

.planetList {
  position: relative;
  height: min(360px, calc(100vw));
  width: min(360px, calc(100vw));
  min-width: 300px;
  min-height: 300px;
  margin: 0 auto;
  z-index: 10;
}

.planet {
  position: absolute;
  width: 72px;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  scale: 1;
  transition: all 0.5s linear;
}

.planet:nth-child(1) {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.planet:nth-child(2) {
  top: 22.5%;
  left: 80%;
  transform: translate(-50%, -50%);
}
.planet:nth-child(3) {
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.planet:nth-child(4) {
  bottom: 22.5%;
  left: 80%;
  transform: translate(-50%, 50%);
}
.planet:nth-child(5) {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.planet:nth-child(6) {
  bottom: 22.5%;
  right: 80%;
  transform: translate(50%, 50%);
}
.planet:nth-child(7) {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.planet:nth-child(8) {
  top: 22.5%;
  left: 22.5%;
  transform: translate(-50%, -50%);
}

.galaxy {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  object-fit: cover;
  z-index: 0;
  scale: 0.5;
  opacity: 0;
}

.image {
  cursor: pointer;
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  aspect-ratio: 1/1;
  margin: 0 auto;
  position: relative;
  z-index: 4;
}

.planetBg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  aspect-ratio: 1/1;
  z-index: 3;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.showPlanetBg {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.multiplier {
  margin-top: -3px;
  font-size: 10px;
  line-height: 12px;
  color: #ffffff;
  text-align: center;
  white-space: nowrap;
  margin-bottom: 0 !important;
  position: relative;
  z-index: 10;
}

.coin {
  width: 10px;
  height: 10px;
  object-fit: cover;
  margin-top: 2px;
}

.planetAnimation {
  animation: planetsVanish 0.5s linear forwards;
}

.planetBgAnimation {
  animation: vanish 0.5s linear forwards;
}

.planetListAnimation {
  animation: scaleAndRotate 5s linear forwards;
}

.galaxyAnimation {
  animation: galaxy 5s linear forwards;
}

@keyframes scaleAndRotate {
  0% {
    transform: scale(1) rotate(0deg);
  }
  5% {
    transform: scale(1.1) rotate(0deg);
  }
  20% {
    transform: scale(1.1) rotate(-180deg);
  }
  40% {
    transform: scale(1.1) rotate(320deg);
  }
  60% {
    transform: scale(1.1) rotate(590deg);
  }
  80% {
    transform: scale(1.1) rotate(770deg);
  }
  90% {
    transform: scale(1.1) rotate(905deg);
  }
  100% {
    transform: scale(1.1) rotate(995deg);
  }
}

@keyframes planetsVanish {
  0% {
    transform: translate(var(--initial-x), var(--initial-y)) scale(1);
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    transform: translate(var(--final-x), var(--final-y)) scale(0);
    opacity: 0;
  }
}

@keyframes galaxy {
  0% {
    opacity: 0;
    transform: scale(0) rotate(0deg);
  }
  5% {
    opacity: 0.8;
    transform: scale(0.6) rotate(0deg);
  }
  10% {
    opacity: 0.8;
    scale: 1.1;
  }
  20% {
    transform: scale(1.05) rotate(-135deg);
  }
  30% {
    transform: scale(1) rotate(-15deg);
  }
  40% {
    opacity: 1;
    transform: scale(0.95) rotate(180deg);
  }
  50% {
    opacity: 1;
    transform: scale(0.9) rotate(405deg);
  }
  60% {
    opacity: 1;
    transform: scale(0.8) rotate(675deg);
  }
  70% {
    opacity: 1;
    transform: scale(0.8) rotate(1035deg);
  }
  80% {
    opacity: 1;
    transform: scale(0.8) rotate(1530deg);
  }
  90% {
    opacity: 0.8;
    transform: scale(0.8) rotate(2070deg);
  }
  100% {
    opacity: 0.1;
    transform: scale(0.1) rotate(2700deg);
  }
}

@keyframes vanish {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
