.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  max-height: 100vh;
  max-width: 375px;
  margin: 0 auto;
  background-color: rgba(22, 3, 41, 0.8);
  transition: background-color 0.3s ease-in-out;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.modalContent {
  background-color: #8c0099;
  border-radius: 20px;
  padding: 16px;
  width: 100%;
  /*overflow: hidden;*/
  transform-origin: bottom;
  animation-fill-mode: forwards;
  position: relative;
}

.closeButton {
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  height: 30px;
  border-radius: 0 20px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  cursor: pointer;
}

.open {
  animation: modalOpen 0.5s ease-in-out;
}

.close {
  animation: modalClose 0.3s ease-in-out;
}

/* Animation for opening the modal */
@keyframes modalOpen {
  from {
    max-height: 0;
    opacity: 0;
  }
  to {
    max-height: 100%;
    opacity: 1;
  }
}

/* Animation for closing the modal */
@keyframes modalClose {
  from {
    max-height: 100%;
    opacity: 1;
  }
  to {
    max-height: 0;
    opacity: 0;
  }
}
