.footer {
  padding: 0 16px 16px;
  margin-top: 24px;
  width: 100%;
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footerItem {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 50%;
  padding: 0 12px;
}

.footerItem:first-child {
  background-image: url(/src/assets/images/line.png);
  background-position: center right;
  background-repeat: no-repeat;
}

.amount {
  display: flex;
  align-items: center;
  margin-top: 3px;
}

.coin {
  width: 16px;
  height: 16px;
  object-fit: cover;
  margin: 0 5px 0 10px;
}
