.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  transition: opacity 0.5s linear;
}

.hideContainer {
  opacity: 0;
  transition: opacity 0.5s linear;
}

.content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bgImg {
  width: 92px;
  height: 92px;
  object-fit: cover;
}

.time {
  z-index: 10;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
