.round {
  margin-top: -32px;
  margin-bottom: 32px;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.round ul {
  display: flex;
  margin-right: 10px;
}

.round ul li {
  width: 32px;
  height: 32px;
  border: 2px solid #d9d9d9;
  background-color: #ba2e98;
  border-radius: 32px;
  overflow: hidden;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: -10px;
}

.round ul li.plus-icon {
  border: none;
  margin: auto;
}

.text {
  font-size: 14px;
  line-height: 18px;
  color: #fff;
  font-weight: 700;
  margin: 0;
}
