.resultSection {
  position: relative;
  padding: 0 16px;
  max-width: 100%;
  margin-bottom: 12px;
  z-index: 10;
}

.text {
  margin: 0;
  background-color: #ba2e98;
  border-radius: 5px 5px 0 0;
  text-align: center;
  color: #fff;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 700;
  position: absolute;
  top: -17px;
  left: 40px;
  padding: 0 6px;
}

.resultHolder {
  border: 1px solid #ba2e98;
  border-radius: 50px;
  padding: 2px 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 48px;
}

.image {
  width: calc(100% / 8 - 2px);
  height: auto;
  max-height: 44px;
  border-radius: 50px;
  object-fit: cover;
}
