@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap");

* {
  margin: 0;
  padding: 0;
  text-transform: none;
}

html {
  -webkit-text-size-adjust: 100%;
}

html body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Plus Jakarta Sans", sans-serif;
  margin: 0;
  padding-top: 0px;
}

a:active {
  outline: none;
  border: none;
}

a:focus {
  -moz-outline-style: none;
  outline: none;
}

body select::-ms-expand {
  display: none;
}

input::-moz-focus-inner,
button::-moz-focus-inner,
select::-moz-focus-inner,
option::-moz-focus-inner,
a::-moz-focus-inner,
button,
a {
  border: 0;
  outline: none;
}

*::-moz-selection {
  color: #fff;
  background: #000;
}

*::-webkit-selection {
  color: #fff;
  background: #000;
}

a,
a:hover,
a:active,
a:focus,
button {
  text-decoration: none;
}

*::after,
*::before,
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
}

ul,
ol {
  margin: 0;
  padding: 0;
  line-height: 1;
}

li {
  list-style: outside none none;
  position: relative;
}

body p {
  color: #333;
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 160%;
  margin: 0 0 15px;
  padding: 0;
}

body p:empty {
  display: none;
  line-height: 0;
}

body p strong {
  font-weight: bold;
}

body a {
  color: #fff;
}

body a:hover {
  color: #fff;
}

* {
  outline: none !important;
  text-decoration: none;
}

body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  font-family: "Plus Jakarta Sans", sans-serif;
  line-height: 1.2;
  margin: 0 0 15px;
}

img {
  margin: 0 auto;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  display: block;
}

.wrapper {
  min-height: 100vh;
  max-width: 375px;
  margin: 0 auto;
  background-image: url(../images/landing_bg.webp);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 100% 103%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  overflow-x: hidden;
}

.home-wrapper {
  justify-content: flex-start !important;
}

.modal-open {
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
}

.planet-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  width: 100%;
  height: min(360px, calc(100vw));
  min-height: 300px;
}

/* HISTORY PAGE*/
.planet-img {
  max-width: 355px;
  margin: 0 auto;
  width: 100%;
}

.modal {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background-color: rgba(22, 3, 41, 0.8);
}

.modal .modal-content {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
  background-color: #8c0099;
  border-radius: 20px;
  padding: 0px;
}

.modal-content .close-button {
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  height: 30px;
  border-radius: 0 20px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  cursor: pointer;
}

.modal-content h3 {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  margin: 15px 15px 15px 30px;
}

/* Style for the radio button */
input[type="checkbox"] {
  display: none;
}

/* Style for the selected state of the label */
input[type="checkbox"]:checked + label {
  font-weight: bold;
  color: #000;
  background-color: #fff;
}

.show {
  visibility: visible;
  opacity: 1;
  display: block;
  transition-delay: 0s;
}

.hide {
  visibility: hidden;
  opacity: 0;
  display: none;
  transition:
    visibility 0s linear 0.33s,
    opacity 0.33s linear;
}

.active {
  opacity: 1;
}
.inactive {
  opacity: 0;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/*  New Page Css */
.fixed {
  position: absolute;
  background-color: #45004b;
  width: 100%;
  height: 100%;
  padding: 15px 15px 0;
  z-index: 99;
}

.fixed-layout .top-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.top-header .btn-top {
  padding: 0;
  background: transparent;
  border-radius: 0;
  border: none;
  text-align: center;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.layout-center h2 {
  font-weight: 600;
  color: #ea00ff;
  font-size: 16px;
}

.layout-menu {
  height: calc(100vh - 165px);
  overflow-y: auto;
}

.layout-menu li a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  padding: 18px 0;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
  position: relative;
}

.layout-menu li a .icon {
  position: absolute;
  left: 70%;
}

.layout-menu li a:hover {
  color: #ea00ff;
}

.layout-bottom {
  display: flex;
  gap: 7px;
  justify-content: flex-end;
  margin: 0 -15px;
  background: linear-gradient(180deg, rgba(140, 0, 153, 0) 0%, #8c0099 100%);
  padding: 15px 25px;
}

.layout-bottom li p {
  color: #fff;
  font-weight: 400;
  font-size: 11px;
  text-align: center;
}

.layout-bottom li .img {
  width: 28px;
  height: 28px;
  margin: 0 auto;
}

.fixed-modal .modal-content {
  top: 50%;
  margin: auto;
  bottom: auto;
  transform: translateY(-50%);
  padding: 25px;
}

.fixed-coin {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.fixed-modal h4 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 600;
  font-size: 14px;
  margin-bottom: 25px;
}

.modal-list li {
  display: flex;
  position: relative;
  justify-content: space-between;
  background: linear-gradient(90deg, #8c0099 5%, #cb1adc 50%, #8c0099 95%);
}

.modal-list li:nth-child(2n) {
  background: transparent;
}

.modal-list li {
  display: flex;
  position: relative;
  justify-content: space-between;
  padding: 0 40px;
}

.modal-list li .planet-img {
  width: 32px;
  height: 32px;
  margin: 0;
}

.fixed-coin span {
  width: 23px;
  height: 23px;
}

.fixed-coin span img {
  width: 100%;
  height: 100%;
}

.fixed-coin h3 {
  margin: 0;
  color: #ffcc00;
  font-size: 35px;
}

.planet-coin {
  display: flex;
  align-items: center;
  gap: 5px;
}

.planet-coin p {
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 0;
}

.modal-list li .select-star {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 15px;
  height: 15px;
}

.online-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 0 20px;
  height: 286px;
  overflow-y: auto;
  margin-bottom: 20px;
}

.online-list li {
  border-radius: 12px;
  background: linear-gradient(180deg, rgba(69, 0, 75, 0.5) 0%, #8c0099 100%);
  padding: 12px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.online-list li .avtar-img,
.winner-top .avtar-img {
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 40px;
  border: 2px solid #d9d9d9;
  flex-shrink: 0;
}

.online-list li .avtar-img img,
.winner-top .avtar-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.online-list .avtar-detail {
  flex: 1 1 0%;
}

.avtar-detail h4 {
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 10px;
}

.avtar-detail .bonus,
.winner-top .winner,
.winner-top .bet-amount {
  display: flex;
  align-items: center;
  gap: 10px;
}

.avtar-detail .bonus p {
  margin: 0;
  color: #fff;
  font-size: 11px;
}

.bonus-price {
  display: flex;
  align-items: center;
  gap: 5px;
}

.avtar-detail .bonus-price p {
  font-size: 12px;
  color: #ffcc00;
}

.winner-popup {
  padding: 45px 0 0;
}

.winner-title {
  text-align: center;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 15px;
  font-weight: 700;
  font-size: 16px;
  justify-content: center;
  margin-bottom: 25px;
}

.winner-list {
  padding: 0 30px;
  height: calc(100vh - 100px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.winner-list li {
  background-color: #35003a;
  border-radius: 15px;
}

.winner-list li .winner-top {
  position: relative;
  padding: 15px;
}

.winner-bottom {
  background: linear-gradient(
    0deg,
    #ff9900 -0.01%,
    #ffa200 10.99%,
    #ffb300 37%,
    #ffbd00 65%,
    #ffc100 100.01%
  );
  border-radius: 0 0 15px 15px;
  padding: 9px 15px;
  text-align: center;
  border-top: 2px dashed #35003a;
  position: relative;
}

.winner-bottom::before,
.winner-bottom::after {
  content: "";
  position: absolute;
  top: -15px;
  left: -15px;
  background-color: #45004b;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.winner-bottom::after {
  left: auto;
  right: -15px;
}

.winner-bottom p {
  font-weight: 700;
  color: #230a40;
  font-size: 13px;
}

.winner-top .winner {
  margin-bottom: 15px;
}

.bet-amount p {
  margin: 0;
  font-size: 12px;
  color: #ea00ff;
}

.bet-amount ul {
  display: flex;
  align-items: center;
}

.bet-amount ul li {
  width: 24px;
  height: 24px;
}

.winner .avtar-detail h4 {
  font-size: 14px;
}

.layout-menu::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: transparent;
  border-radius: 10px;
}

.layout-menu::-webkit-scrollbar {
  width: 5px;
  border-radius: 10px;
  background-color: transparent;
  display: none;
}

.layout-menu::-webkit-scrollbar-thumb {
  background-color: hsla(260, 11%, 85%, 1);
  border: 2px solid #555555;
  border-radius: 10px;
}
.more-item {
  margin: auto;
  cursor: pointer;
}

.modal-content .modal-header.loss {
  top: -75%;
}
