.container {
  width: 100%;
}

.planet {
  width: 60px;
  height: 60px;
  object-fit: cover;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1000;
  margin-top: -200px;
  animation: planetAnimation 1s ease-in-out forwards;
  border-radius: 50%;
}

/* WIN */
.winView {
  position: relative;
  display: flex;
  height: 240px;
  padding: 24px 12px 12px;
}

.confetti {
  width: 100%;
  height: 240px;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.winContent {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 auto;
}

.winTitle {
  font-family: Racing Sans One;
  font-size: 26px;
  font-weight: 900;
  line-height: 32px;
  font-style: italic;
  text-align: center;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #ffffff;
  margin: 0 0 12px 0;
}

.winText {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  color: #ffffff;
}

.winAmount {
  display: flex;
  align-items: center;
  margin-bottom: 6px;
}

.coinImg {
  width: 32px;
  height: 32px;
  object-fit: cover;
  margin-right: 6px;
}

.winCoins {
  font-family: Plus Jakarta Sans;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  color: rgba(255, 204, 0, 1);
}

/* MISSED */
.missView {
  display: flex;
  align-items: center;
  padding: 24px 12px 12px;
}

.emoji {
  font-size: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.missedContent {
  flex: 1;
  margin-left: 14px;
}

.missedTitle {
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
  color: #ffffff !important;
}

.missedText {
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.6) !important;
  margin-bottom: 0 !important;
}

/* WINNERS */
.winnerContainer {
  margin-top: -50px;
}

.winnerTitle {
  background: linear-gradient(90deg, #8c0099 10%, #cb1adc 50%, #8c0099 90%);
  color: #ffffff;
  font-size: 12px;
  line-height: 16px;
  font-weight: 600;
  text-align: center;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 12px 0 64px;
  text-transform: uppercase;
}

.winnersList {
  position: relative;
}

.winnersStage {
  width: 100%;
  height: auto;
  margin-bottom: -16px;
}

.winner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: 0;
}

.winner:nth-child(2) {
  left: 50%;
  transform: translate(-50%, -50%);
}

.winner:nth-child(3) {
  left: 17%;
  transform: translateX(-50%);
}

.winner:nth-child(4) {
  left: 84%;
  top: 10px;
  transform: translateX(-50%);
}

.winnerImage {
  width: 52px;
  height: 52px;
  object-fit: cover;
  border: 2px solid #cbcbcb;
  border-radius: 50%;
}

.winnerMedal {
  width: 24px;
  height: auto;
  object-fit: contain;
  margin-top: -22px;
}

.name,
.amount {
  color: #230a40;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  margin-bottom: 0;
}

.amount {
  font-weight: 500;
}

.amountHolder {
  display: flex;
  align-items: center;
  justify-content: center;
}

.coin {
  width: 12px;
  height: 12px;
  object-fit: cover;
  margin: 0 4px 0 0;
}

@keyframes planetAnimation {
  0% {
    transform: scale(0);
    margin-top: -200px;
    opacity: 0;
  }
  30% {
    transform: scale(1);
    margin-top: -200px;
    opacity: 0.8;
  }
  70% {
    transform: scale(2);
    margin-top: -200px;
    opacity: 1;
  }
  100% {
    transform: scale(1);
    margin-top: -46px;
  }
}
