.currentBetView {
  width: 100%;
  padding: 0 16px;
  color: #fff;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  margin-top: 6px;
}

.currentBet {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 4px;
  height: 24px;
}

.planetsHolder {
  display: flex;
  align-items: center;
}

.planetImage {
  width: 24px;
  height: 24px;
  object-fit: cover;
  margin: 0px 2px;
}

.betAmountHolder {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.betAmount {
  color: #ffcc00;
}

.coin {
  width: 10px;
  height: 10px;
  object-fit: cover;
  margin: 0 5px 0 10px;
}
